import {
  Layout,
  Tabs,
  Input,
  Select,
  Form,
  Typography,
  Button,
  Row,
  Col,
  Space,
} from "antd";
import ContainerIdentify from "../containers/containersCommonFile/containerIdentify";
import ContainerEmployee from "../containers/containersCommonFile/containeEmployee";
import ContainerRecission from "../containers/containersRescission/containerRecission";
import ContainerObservation from "../containers/containersCommonFile/containerObservation";
import FormPageMovimentation from "../layoutPage/FormPageMovimentation";
import FormPageRescission from "../layoutPage/FormPageRescission";
import FormPageVacation from "../layoutPage/FormPageVacation";
//import "../containers/container.css";

const { Header, Footer, Content } = Layout;
const { Title } = Typography;

const { TabPane } = Tabs;
const { Option } = Select;

function LayoutPage() {
  return (
    <>
      <Layout>
        <Header className="CVCHeader"></Header>
        <Content className="CVCContent">
          <Typography>
            <Title level={2}>Reciso</Title>
            <Title level={2} style={{ marginTop: "-20px" }}>
              Tarefa: Solicitao
            </Title>
          </Typography>
          <Form wrapperCol={{ span: 12 }} labelAlign="left">
            <Row gutter={20}>
              <Col span={6}>
                <Form.Item
                  name={["subject"]}
                  label={["Assunto"]}
                  labelCol={{ span: 10 }}
                >
                  <Input value={"boa tarde"} />
                </Form.Item>
                <Col span={6}></Col>
              </Col>
            </Row>
            <Row gutter={20}>
              <Col span={6}>
                <Form.Item
                  name={["sendTo"]}
                  label={["Enviar para"]}
                  labelCol={{ span: 10 }}
                >
                  <Select>
                    <Option value="RH Reciso">Rh Reciso</Option>
                  </Select>
                </Form.Item>
              </Col>
              <Col span={12}>
                <Button type="primary">Enviar</Button>
              </Col>
            </Row>
          </Form>
          <Tabs tabPosition="top" defaultActiveKey="1">
            <TabPane tab={["Formulrio"]} key={"1"}>
              <Space direction="vertical" size={"large"}>
                <a href="http://localhost:3000/form/movimentacao">
                  Formulrio de Movimentao
                </a>
                <a href="http://localhost:3000/form/rescisao">
                  Formulrio de Resciso
                </a>
                <a href="http://localhost:3000/form/ferias">
                  Formulrio de Frias
                </a>
              </Space>
            </TabPane>
            <TabPane tab={["Anexos"]} key={"2"}>
              <FormPageRescission />
            </TabPane>
            <TabPane tab={"Histricos e Observaes"} key={"3"}>
              <ContainerRecission />
            </TabPane>
            <TabPane tab={"Andamento do Processo"} key={"4"}>
              <FormPageVacation />
            </TabPane>
          </Tabs>
        </Content>
        <Footer className="CVCFooter"></Footer>
      </Layout>
    </>
  );
}

export default LayoutPage;
