import React from "react";
import { Route, BrowserRouter, Routes } from "react-router-dom";
import FormPageMovimentation from "../../layoutPage/FormPageMovimentation";
import FormPageRescission from "../../layoutPage/FormPageRescission";
import FormPageVacation from "../../layoutPage/FormPageVacation";
import LayoutPage from "../../layoutPage/LayoutPage";

function RoutePages() {
  return (
    <BrowserRouter>
      <Routes>
        <Route path="/" element={<LayoutPage />} />
        <Route path="/form/movimentacao" element={<FormPageMovimentation />} />
        <Route path="/form/rescisao" element={<FormPageRescission />} />
        <Route path="/form/ferias" element={<FormPageVacation />} />
      </Routes>
    </BrowserRouter>
  );
}

export default RoutePages;
